import styled from "styled-components";

const location = window.location.pathname

const TopSectionContainer = styled.div`
  display: flex;
  justify-content: center;
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
  top: 0;
  left: 0;
  background-color: #1756dd32;
  flex-direction: column;
  align-items: center;
  z-index: 99;
`

const Logo = styled.h1`
  margin: 0;
  margin-bottom: 1em;
  color: #fffd;
  font-weight: 700;
  font-size: 40px;
  text-align: center;
`

const Slogan = styled.h4`
  margin: 0;
  margin-bottom: 1.2em;
  color: #fffa;
  font-weight: 500;
  font-size: 24px;
  max-width: 500px;
  text-align: center;
`

const Paragraph = styled.p`
  display: block;
  margin: 0;
  margin-bottom: 2.4em;
  color: #fffd;
  font-size: 15px;
  line-height: 1.5;
  font-weight: 300;
  max-width: 700px;
  text-align: center;
`

const Enter = styled.button`
  outline: none;
  border: none;
  color: #ffff;
  background-color: #5c6ac48d;
  font-size: 16px;
  font-weight: 500;
  border-radius: 8px;
  padding: 10px 2em;
  cursor: pointer;
  border: 3px solid transparent;
  transition: all 350ms ease-in-out;

  a {
    color: #fffb;
    text-decoration: none;
    transition: all 350ms ease-in-out;
  }

  &:hover {
    background-color: #5c6ac4ff;
  }
`

export function TopSection() {
  return (
    <TopSectionContainer>
      <Logo>Thought Repository</Logo>
      <Slogan>탄소중립(Carbon Neutrality)</Slogan>
      <Paragraph>
        탄소중립은 인간활동에 따른 온실가스 배출량이 전 지구적 이산화탄소 흡수량과 균형을 이뤄 대기 중 이산화탄소 농도가 더 높아지지 않는 것을 의미한다. 즉 이산화탄소 순 배출량이 '0'이 되도록 하는 것으로 '넷 제로(Net Zero), 탄소 제로(Carbon Zero)'라고도 한다.
      </Paragraph>
      <Paragraph>
        탄소중립을 달성하려면 차량과 공장의 화석 연료 연소 등과 같은 인위적 배출을 최대한 줄이고, 나머지 이산화탄소는 습지, 숲 복원 등 흡수원을 확대해 흡수량을 늘리거나 네거티브 배출 기술(Negative Emission Technique, NET)로 대기 중 이산화탄소를 제거해야 한다.
      </Paragraph>
      <a href={location.includes("page")?'../world':'world'}><Enter>탄소배출 현황</Enter></a>
    </TopSectionContainer>
  )
}
